function Oauth() {
    this.setToken = function () {
        const accessToken = window.localStorage.getItem('vikon_access_token');
        const refreshToken = window.localStorage.getItem('vikon_refresh_token');
        const code = window.localStorage.getItem('vikon_code');

        return new Promise((resolve) => {
            if (accessToken) {
                const promiseCheckAccessToken = checkAccessToken(accessToken);
                promiseCheckAccessToken.then(() => {
                    resolve(true);
                }).catch(() => {
                    noToken();
                });
            } else {
                noToken();
            }

            function noToken() {
                if (refreshToken) {
                    const promiseRefreshAccessToken = refreshAccessToken(refreshToken);
                    promiseRefreshAccessToken.then(() => {
                        resolve(true);
                    }).catch(() => {
                        noRefreshToken();
                    });
                } else {
                    noRefreshToken();
                }
            }

            function noRefreshToken() {
                if (code) {
                    const promiseGetAccessToken = getAccessToken(code);
                    promiseGetAccessToken.then(() => {
                        resolve(true);
                    }).catch(() => {
                        resolve(false);
                    });
                } else {
                    resolve(false);
                }
            }
        }).catch((e) => {
            throw e;
        });
    };

    this.getToken = function () {
        return window.localStorage.getItem('vikon_access_token');
    };

    function checkAccessToken(accessToken) {
        return new Promise((resolve, reject) => {
            $_vikon.post(`${apiDomen}oauth2/resource/checkToken`, {access_token: accessToken})
                .done(() => {
                    resolve(true);
                })
                .fail(() => {
                    reject(new Error('Токен доступа не прошел проверку.'));
                });
        });
    }

    function refreshAccessToken(refreshToken) {
        return new Promise((resolve, reject) => {
            $_vikon.post(`${domen}/sveden/update/refresh_access_token.php`, {refresh_token: refreshToken})
                .done((res) => {
                    if (!res.success || !res.access_token || !res.refresh_token) {
                        reject(new Error('Не удалось обновить токен доступа.'));
                    } else {
                        window.localStorage.setItem('vikon_access_token', res.access_token);
                        window.localStorage.setItem('vikon_refresh_token', res.refresh_token);
                        resolve(true);
                    }
                })
                .fail(() => {
                    reject(new Error('Не удалось обновить токен доступа.'));
                });
        });
    }

    function getAccessToken(code) {
        return new Promise((resolve, reject) => {
            $_vikon.post(`${domen}/sveden/update/get_access_token.php`, {code, domen}, (res) => {
                if (res.success) {
                    window.localStorage.setItem('vikon_access_token', res.access_token);
                    window.localStorage.setItem('vikon_refresh_token', res.refresh_token);
                    resolve(true);
                }
                reject(new Error('Не удалось получить токен доступа.'));
            }).fail(() => {
                reject(new Error('Не удалось получить токен доступа.'));
            });
        });
    }

    this.redirectUpdatePage = () => {
        window.location.href = `${domen}/sveden/update`;
    };
}

// @deprecated
function Updater() {
    let url;
    let data;
    let onSuccess;
    let attempts = 0;
    let methodAjax = 'GET';
    let unlockedRuningOnError = true;

    this.setMethodAjax = function (methodAjaxVal) {
        methodAjax = methodAjaxVal;
    };

    this.setUrl = function (urlVal) {
        url = urlVal;
    };

    this.setData = function (dataVal) {
        data = dataVal;
    };

    this.setOnSuccess = function (onSuccessVal) {
        onSuccess = onSuccessVal;
    };

    this.setUnlockedRuningOnError = function (unlockedRuningIfError) {
        unlockedRuningOnError = unlockedRuningIfError;
    };

    this.resetSettings = function () {
        url = '';
        data = {};
        onSuccess = undefined;
        attempts = 0;
        methodAjax = 'GET';
        unlockedRuningOnError = true;
    };

    this.sendAjax = function (critical, nameFunc) {
        if (critical === undefined) {
            critical = true;
        }
        if (methodAjax === undefined) {
            methodAjax = 'GET';
        }
        return new Promise((resolve) => {
            data.debug_mode = debugMode;
            data.no_del_file_php = noDelFilePhp;
            $_vikon.ajax({
                type: methodAjax,
                url,
                headers: {
                    Accept: 'application/json',
                },
                dataType: 'json',
                data,
                success(res) {
                    if (onSuccess !== undefined) {
                        onSuccess(res);
                        return resolve(res);
                    }
                    const isErrToken = res.error !== undefined && (res.error === 'invalid_token' || res.error === 'Authorization failed');
                    if (res.success) {
                        if (res.message) {
                            processContainer.prepend(`<p>${res.message}</p>`);
                        }
                        if (critical) {
                            return resolve(res);
                        }
                    } else if (isErrToken && attempts === 0 && nameFunc !== undefined) {
                        const promiseSetToken = oauth.setToken();
                        promiseSetToken.then((success) => {
                            if (success) {
                                attempts++;
                                data.access_token = oauth.getToken();
                                const promiseSend = updater.sendAjax();
                                promiseSend.then((res) => {
                                    eval(nameFunc);
                                });
                            } else {
                                oauth.redirectUpdatePage();
                            }
                        });
                    } else if (res.message) {
                        processContainer.prepend(`<p class="text-danger">Ошибка: ${res.message}</p>`);
                        progressbarContainer.hide();
                        if (unlockedRuningOnError) {
                            completeErrorAsync(); // без await
                        }
                    }
                    if (!critical) {
                        return resolve(res);
                    }
                },
                error(xhr, status, error) {
                    processContainer.prepend(`<p class="text-danger">Ошибка: ${status} ${error}</p>`);
                    if (xhr.status === 401 && attempts === 0) {
                        const promiseSetToken = oauth.setToken();
                        promiseSetToken.then((success) => {
                            if (success) {
                                attempts++;
                                data.access_token = oauth.getToken();
                                const promiseSend = updater.sendAjax();
                                promiseSend.then((res) => {
                                    return resolve(res);
                                });
                            } else {
                                oauth.redirectUpdatePage();
                            }
                        });
                    } else if (unlockedRuningOnError) {
                        completeErrorAsync(); // без await
                    }
                },
            });
        });
    };

    this.addMessage = function (msg) {
        processContainer.prepend(`<p>${msg}</p>`);
    };
}

function ProgressbarControl() {
    this.setActive = function () {
        progressbar.addClass('active');
    };

    this.setDeactive = function () {
        progressbar.removeClass('active');
    };

    this.setVal = function (progressVal) {
        progressbar.css('width', `${progressVal}%`);
        progressbar.html(`${progressVal}%`);
    };
}

function disabledAccess(access, obj) {
    if (access.indexOf(obj.val()) === -1) {
        if (obj.is(':checked')) {
            obj.attr('checked', false);
        }
        obj.attr('disabled', true);
        obj.parent().addClass('disabled');
        obj.next().prepend('<span class="text-danger">Нет прав</span> ');
    }
}

if (window.localStorage) {
    var oauth = new Oauth();
    var updater = new Updater();
    var progressControl = new ProgressbarControl();

    oauth.setToken()
        .then(async (success) => {
            if (success) {
                const accessList = await getAccessListAsync();
                $_vikon('input[name="part"]').each(function () {
                    disabledAccess(accessList, $_vikon(this));
                });
                disabledAccess(accessList, $_vikon('input[name="sync"]'));
                if (accessList.indexOf('admin_mode') !== -1) {
                    $_vikon('#admin-mode-panel').removeClass('hide');
                }
                if (accessList.indexOf('no_update_abitur') !== -1) {
                    $_vikon('#abitur').closest('li').remove();
                }
                if (accessList.indexOf('no_del_file_php') !== -1) {
                    noDelFilePhp = 1;
                }

                waitLoad.remove();
                yesEnter.show();
                btnExit.show();

                const version = await getVersionAsync();
                if (version === null || currentVersion !== version) {
                    newVersion.removeClass('label-warning').addClass('label-success').html(version);
                } else {
                    newVersion.removeClass('label-warning').addClass('label-default');
                    newVersion.html('обновления отсутствуют');
                }
            } else {
                waitLoad.remove();
                noEnter.show();
            }
        })
        .catch((e) => {
            messageContainer.html(e.message).show();
        });
} else {
    waitLoad.remove();
    messageContainer.html(
        'Объект localStorage не поддерживается данным браузером. Пожалуйста, воспользуйтесь другим Internet-браузером.',
    ).show();
}

