async function get() {
    progressControl.setActive();
    progressControl.setVal(10);
    if ($_vikon('input[name="no_core"]').is(':checked')) {
        processContainer.prepend('<p>Обновление ядра отключено.</p>');
        await pullParts();
        getCountFiles();
    } else {
        const version = await getCoreAsync();
        await unpackAsync(version);
        await pullParts();
        getCountFiles();
    }
}

async function getCoreAsync() {
    progressControl.setActive();
    progressControl.setVal(10);
    processContainer.prepend('<p>Начинается обновление скриптов обновления.</p>');

    const cfg = {
        type: 'get',
        url: `${domen}/sveden/update/get_core.php`,
        headers: {
            Accept: 'application/json',
        },
        dataType: 'json',
        data: {},
    };
    try {
        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth);
        if (respData.forward_code !== 200 || !respData.version) {
            throw new Error(respData.message ? respData.message : 'Неопознанная ошибка');
        }
        processContainer.prepend(`<p>${respData.message}</p>`);
        return respData.version;
    } catch (e) {
        processContainer.prepend(`<p class="text-danger">${e.message}`);
        await showUpdateEndMessage(false);
        throw e;
    }
}

async function unpackAsync(version) {
    progressControl.setVal(20);
    const cfg = {
        type: 'get',
        url: `${domen}/sveden/update/unpack.php`,
        headers: {
            Accept: 'application/json',
        },
        dataType: 'json',
        data: {version},
    };
    try {
        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth);
        if (respData.forward_code !== 200) {
            throw new Error(respData.message ? respData.message : 'Неопознанная ошибка');
        }
    } catch (e) {
        processContainer.prepend(`<p class="text-danger">${e.message}`);
        await completeErrorAsync();
        throw e;
    }
}

async function pullParts() {
    const partsKeys = [];
    $_vikon('input[name="part"]:checked').each(function () {
        partsKeys.push($_vikon(this).val());
    });

    for (let index = 0; index < partsKeys.length; index++) {
        const part = partsKeys[index];
        progressControl.setVal(20 + index * 3);
        try {
            const {operationIdentity, ttl} = await createPartRequestAsync(part);
            await waitPartAsync(operationIdentity, ttl);
            await checkPartResultAsync(operationIdentity, part);
            await downloadPartResultAsync(operationIdentity, part);
        } catch (e) {
            processContainer.prepend(`<p class="text-danger">${e.message}</p>`);
            await completeErrorAsync();
            throw e;
        }
    }
}

async function waitPartAsync(operationIdentity, ttl) {
    const ttlMax = ttl || 350;

    const sleep = (t) => {
        return new Promise((r) => {
            return setTimeout(r, t);
        });
    };

    const cfg = {
        type: 'get',
        url: `${domen}/sveden/update/status_generate_part.php`,
        headers: {
            Accept: 'application/json',
        },
        dataType: 'json',
        data: {operation_identity: operationIdentity},
    };

    const startTime = Date.now() / 1000;
    try {
        let sleepTime = 3;
        let iterCount = 0;
        while (Date.now() / 1000 - startTime < ttlMax) {
            await sleep(sleepTime * 1000);
            iterCount += 1;
            sleepTime = iterCount > 3 ? sleepTime + (iterCount - 3) : sleepTime;
            let respData = {};
            try {
                ({respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth));
            } catch (e) {
                throw new Error('не удалось получить статус процесса генерации');
            }

            if (respData.forward_code !== 200) {
                throw new Error(respData.message ? respData.message : 'неопознанная ошибка');
            }

            if (respData.status === WORK_WASTED) {
                throw new Error('не удалось сгенерировать архив. Попробуйте запусить процесс обновления заново.');
            }

            if (respData.status === WORK_DONE) {
                return;
            }
        }
        throw new Error('не удалось дождаться результата генерации за приемлемое время');
    } catch (e) {
        throw new Error(`Ошибка генерации архива с разделом: ${e.message}`);
    }
}

async function downloadPartResultAsync(operationIdentity, part) {
    try {
        const cfg = {
            type: 'post',
            url: `${domen}/sveden/update/result_download_part.php`,
            headers: {
                Accept: 'application/json',
            },
            dataType: 'json',
            data: {operation_identity: operationIdentity, part},
        };
        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth);
        if (respData.forward_code !== 200) {
            throw new Error(respData.message ? respData.message : 'неопознанная ошибка');
        }
        processContainer.prepend(`<p>${respData.message}`);
    } catch (e) {
        throw new Error(`Ошибка при загрузке результатов генерации архива с разделом: ${e.message}`);
    }
}

async function checkPartResultAsync(operationIdentity, part) {
    try {
        const cfg = {
            type: 'post',
            url: `${domen}/sveden/update/result_check_generate_part.php`,
            headers: {
                Accept: 'application/json',
            },
            dataType: 'json',
            data: {operation_identity: operationIdentity, part},
        };
        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth);
        if (respData.forward_code !== 200) {
            throw new Error(respData.message ? respData.message : 'неопознанная ошибка');
        }
    } catch (e) {
        throw new Error(`Ошибка при получении результатов генерации архива с разделом: ${e.message}`);
    }
}

async function createPartRequestAsync(part) {
    try {
        const cfg = {
            type: 'post',
            url: `${domen}/sveden/update/request_generate_part.php`,
            headers: {
                Accept: 'application/json',
            },
            dataType: 'json',
            data: {part},
        };
        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth);
        if (respData.forward_code !== 200 || respData.operation_identity === undefined || respData.ttl === undefined) {
            throw new Error(respData.message ? respData.message : 'Неопознанная ошибка');
        }
        return {operationIdentity: respData.operation_identity, ttl: respData.ttl};
    } catch (e) {
        throw new Error(`Ошибка в инициализации генерации архива: ${e.message}`);
    }
}

async function completeAsync() {
    const cfgCleaner = {
        type: 'get',
        url: `${domen}/sveden/update/cleaner.php`,
        headers: {
            Accept: 'application/json',
        },
        dataType: 'json',
    };

    try {
        progressControl.setVal(80);
        await versionUpAsync();

        progressControl.setVal(90);
        updater.addMessage('Начинается удаление временных файлов.');

        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfgCleaner, false, oauth);
        if (respData.forward_code !== 200) {
            throw new Error(respData.message);
        }
        await showUpdateEndMessage(true);
        updater.addMessage('Обновление завершено');
    } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
        clearTmpFilesErrorLayout.show().html(e.message);
    }
    progressControl.setVal(100);
}

async function versionUpAsync() {
    try {
        const cfg = {
            type: 'post',
            url: `${domen}/sveden/update/complete.php`,
            headers: {
                Accept: 'application/json',
            },
            dataType: 'json',
            data: {},
        };
        const {respData} = await requestToVuzRemoteScriptWithRepetitionAsync(cfg, true, oauth);
        if (respData.forward_code !== 200) {
            throw new Error(respData.message ? respData.message : 'неопознанная ошибка');
        }
    } catch (e) {
        throw new Error(`Ошибка при попытке отправить запрос на завершение обновления: ${e.message}`);
    }
}

function getCountFiles() {
    progressControl.setVal(70);
    updater.resetSettings();
    updater.setUrl(`${domen}/sveden/update/get_count_files.php`);
    updater.setData({access_token: oauth.getToken()});
    updater.addMessage('Запрос количества файлов.');
    updater.sendAjax(true, 'res.count > 0 ? getPackFiles(res.count, 0) : completeAsync()')
        .then((res) => {
            if (res.count > 0) {
                getPackFiles(res.count, 0);
            } else {
                $_vikon('input[name="sync"]').is(':checked')
                    ? getFilesForSync()
                    : completeAsync(); // пока без await
            }
        });
}

function getPackFiles(countFiles, startFile) {
    if (startFile <= countFiles) {
        updater.resetSettings();
        updater.setUrl(`${domen}/sveden/update/get_pack_files.php`);
        updater.setData({access_token: oauth.getToken(), startFiles: startFile, filesForPart: countFilesInBatch});
        updater.sendAjax(true, `getPackFiles(${countFiles}, ${startFile + countFilesInBatch})`)
            .then((res) => {
                getPackFiles(countFiles, startFile + countFilesInBatch);
            });
    } else {
        $_vikon('input[name="sync"]').is(':checked')
            ? getFilesForSync()
            : completeAsync(); // пока без await
    }
}

function getFilesForSync() {
    progressControl.setVal(70);
    updater.resetSettings();
    updater.setUrl(`${domen}/sveden/update/get_files_for_sync.php`);
    updater.setData({access_token: oauth.getToken()});
    updater.sendAjax(true, 'if (res.files.length > 0) { filesForSync = res.files; getPackFilesForSync(0); } else { completeAsync();}')
        .then((res) => {
            if (res.files.length > 0) {
                filesForSync = res.files;
                getPackFilesForSync(0);
            } else {
                completeAsync();// пока без await
            }
        });
}

function getPackFilesForSync(fileNumber) {
    if (fileNumber < filesForSync.length) {
        const filesPack = [];
        for (let i = fileNumber; i < (fileNumber + countFilesInBatch); i++) {
            if (i in filesForSync) {
                filesPack[filesPack.length] = filesForSync[i];
            }
        }
        updater.resetSettings();
        updater.setUrl(`${domen}/sveden/update/get_pack_files_for_sync.php`);
        updater.setData({access_token: oauth.getToken(), files: filesPack});
        updater.setMethodAjax('POST');
        updater.sendAjax(true, `getPackFilesForSync(${fileNumber + countFilesInBatch})`)
            .then((res) => {
                getPackFilesForSync(fileNumber + countFilesInBatch);
            });
    } else {
        updater.addMessage('Синхронизация файлов завершена.');
        completeAsync(); // пока без await();
    }
}

function showUpdateEndMessage(success) {
    return new Promise((resolve) => {
        progressControl.setDeactive();
        if (success) {
            updateComplete.removeClass('alert-danger').addClass('alert-success');
            updateComplete.html('Обновление завершено');
        } else {
            updateComplete.removeClass('alert-success').addClass('alert-danger');
            updateComplete.html('Обновление завершено c ошибками');
        }
        updateComplete.show();
        resolve(success);
    });
}

async function getAccessListAsync() {
    const cfg = {
        type: 'get',
        url: `${apiDomen}pull_updates/assist/getUserAccessListJson`,
        headers: {
            Accept: 'application/json',
        },
        dataType: 'json',
        data: {},
    };

    try {
        const {respData} = await requestToWorldWithRepetitionAsync(cfg, false, oauth);
        return respData.access;
    } catch (sAjaxError) {
        const errDetail = tryExtractErrorFromSimpleAjaxPromise(sAjaxError);
        throw new Error(`Ошибка при получении прав пользователя${(errDetail ? `: ${errDetail}` : '')}`);
    }
}

